$small: 600px;

@mixin button-transparent() {
  width: 87px;
  height: 34px;
  border-width: 2px;
  background: linear-gradient(white, white) padding-box, 
  linear-gradient(0deg, #8BA4F9, #BC9CFF) border-box;
  border-radius: 22px;
  border-style: solid;
  border-color: transparent;
  cursor: pointer;
  font-weight: bold;
  font-size: 12px;
  text-transform: uppercase;
  color: #BC9CFF;
}



.site-header{
  position: relative;
  background-color: #f0fffc;
}

.text-header{
  text-decoration: none;
  font-size: 14px;
  color: rgba(31, 32, 65, 0.5);
  font-weight: normal;
}

.site-header__wrapper {
  padding-top: 1rem;
  padding-bottom: 1rem;

  @media (min-width: $small) {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding-top: 0;
    padding-bottom: 0;
  }
}

.wrapper {
  max-width: 680px;
  padding-left: 1rem;
  padding-right: 1rem;
  margin-left: auto;
  margin-right: auto; 
}


.nav__toggle {
  display: none;
  list-style-type: none;

  @media (max-width: 599px) {
    display: block;
    position: absolute;
    right: 1rem;
    top: 1rem;
    @include button-transparent();
  }
}

.nav__wrapper {
  @media (min-width: $small) {
    display: flex;
  }

  @media (max-width: 599px) {
    position: absolute;
    top: 100%;
    right: 0;
    left: 0;
    z-index: -1;
    background-color: #f0fffc;
    visibility: hidden;
    opacity: 0;
    transform: translateY(-100%);
    transition: transform 0.3s ease-out, opacity 0.3s ease-out;

    &.active {
      visibility: visible;
      opacity: 1;
      transform: translateY(0);
      padding: 0.5rem 15.5rem;
      
    }
  }
}


.nav__item {
  a {
    display: block;
    padding: 0.5rem 1.5rem;
  }
  list-style-type: none;
  white-space: nowrap;
}

.button-comein{
  @include button-transparent();

}


